.number-options{
    position: relative;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 200px;
}


.input-field{
    height: 50px;
    width: 35vw;
    margin-right: 20px;
    text-align: center;
    font-size: larger;
}

.btn-submit{
    text-align: center;
    padding: 10px 20px;
    border-radius: 10px;
    margin-left: 10px;
    background-color: lawngreen;
    font-size: 40px;
}
.btn-submit.btn-submit:hover{
    border:1px solid yellow;
}