ul{
    list-style: none;
    width: 60vw;
    flex-wrap: wrap;
    font-size: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    background-color: black;
    border: 1px solid white;
    border-radius: 10px;
    padding: 10px;
}

.modal{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.btn-cerrar-modal{
    height: 80px;
    width: 80px;
    margin-top: 20px;
    background-color: rgba(255, 11, 11, 0.8);
    border-radius: 10px;
}

@media screen and (max-width: 550px) {

    ul{
        width: 80vw;
        font-size: 25px;
    }
}