.random-number {
    position: fixed;
    top: 30%;
    font-size: 1.5rem;
    border: 1px solid black;
    padding: 6vh 12vh;
}

.random-number-containter {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

@media screen and (max-width: 750px) {

    .random-number{
        padding: 3vh 6vh;
    }
}
@media screen and (max-width: 500px) {

    .random-number{
        padding: 2vh 4vh;
        font-size: 1.2rem;
    }
}
@media screen and (max-width: 385px) {

    .random-number{
        padding: 1vh 2vh;
        font-size: 1.1rem;
    }
}