.help-button{
    position: fixed;
    top: 3vw;
    right: 3vw;
    width: 7vw;
    height: 7vw;
    font-size: 40px;
    border-radius: 10px;
    background-color: yellow;
}
@media screen and (max-width: 650px) {
    .help-button{
        height: fit-content;
        width: 10vw;
    }
}